.message {
    position: relative;
    align-items: flex-end;
    display: flex;
    gap: 0px;
    flex-direction: column;
    gap: 5px;
}

.imageAndText {
    display: flex;
    gap: 10px;

}

.imageAndText img {
    order: 1;
    width: 60px;
}

.text {
    padding: 10px;
    border-radius: 10px;
    background: #0066CC;
    box-shadow: 0 1px 2px 0;
}

.text:after {

}

.name {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: bold;
}

.messageText {
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 400;
}

.time {
    padding: 0px 10px;
}