.friendMessage {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.friendImageAndText {
    display: flex;
    gap: 10px;
}

.friendImageAndText img {
    width: 60px;
}

.friendText {
    padding: 10px;
    border-radius: 10px;
    background: rgba(245, 247, 251, 1);
    box-shadow: 0 1px 2px 0;
}

.friendText:after {
    content:'';
}

.friendName {
    font-size: 16px;
    font-weight: bold;
}

.friendMessageText {
    font-size: 16px;
    font-weight: 400;
}

.friendTime {
    padding: 0px 10px;
}
